/* Mobile-first base styles */
.portfolio-root {
  position: relative;
  z-index: 1;
  min-height: 100vh;
  color: #f3f3f3;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  padding: 0;
  margin: 0;
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
}

/* Mobile-first hero section */
.hero {
  text-align: center;
  padding: 2rem 1rem 1.5rem 1rem;
  background: linear-gradient(90deg, #0f2027 0%, #2c5364 100%);
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  box-shadow: 0 4px 24px rgba(0,0,0,0.2);
  position: relative;
  z-index: 1;
  margin-bottom: 1rem;
}

.hero h1 {
  font-size: 2rem;
  margin: 0;
  letter-spacing: 1px;
  font-weight: 700;
  line-height: 1.2;
}

.subtitle {
  font-size: 1rem;
  color: #61dafb;
  margin: 0.5rem 0 1rem 0;
  line-height: 1.3;
}

.hero-links {
  display: flex;
  justify-content: center;
  gap: 0.8rem;
  flex-wrap: wrap;
  margin-top: 1rem;
}

.hero-links a {
  color: #fff;
  background: #61dafb;
  padding: 0.6rem 1rem;
  border-radius: 1.5rem;
  text-decoration: none;
  font-weight: 500;
  transition: background 0.2s, color 0.2s, transform 0.2s;
  box-shadow: 0 2px 8px rgba(97,218,251,0.15);
  font-size: 0.9rem;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 100px;
}

.hero-links a:hover {
  background: #21a1f3;
  color: #fff;
  transform: translateY(-2px) scale(1.05);
}

/* Mobile-first sections */
section {
  margin: 1.5rem auto;
  max-width: 95%;
  padding: 1rem 0.8rem;
  background: rgba(30, 34, 40, 0.85);
  border-radius: 1rem;
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  will-change: opacity, transform;
}

section h2 {
  color: #61dafb;
  margin-bottom: 0.8rem;
  font-size: 1.6rem;
  font-weight: 600;
  text-align: center;
}

/* Mobile-first skills grid */
.skills-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}

.skill-category {
  background: #23272f;
  border-radius: 0.8rem;
  padding: 0.8rem 1rem;
  box-shadow: 0 1px 8px rgba(97,218,251,0.05);
  will-change: opacity, transform;
}

.skill-category h3 {
  color: #21a1f3;
  margin-bottom: 0.5rem;
  font-size: 1rem;
  text-align: center;
}

.skill-category ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.skill-category li {
  margin-bottom: 0.3rem;
  font-size: 0.9rem;
  text-align: center;
  line-height: 1.4;
}

/* Mobile-first experience cards */
.exp-card {
  background: #23272f;
  border-radius: 0.8rem;
  margin-bottom: 1rem;
  padding: 1rem;
  box-shadow: 0 1px 8px rgba(97,218,251,0.05);
  transition: transform 0.2s;
  will-change: opacity, transform;
}

.exp-card:hover {
  transform: translateY(-3px) scale(1.01);
}

.exp-card h3 {
  margin: 0 0 0.3rem 0;
  font-size: 1.1rem;
  color: #fff;
  text-align: center;
}

.exp-card h3 span {
  color: #61dafb;
  font-weight: 400;
  font-size: 0.9rem;
  display: block;
  margin-top: 0.2rem;
}

.exp-meta {
  color: #b0b0b0;
  font-size: 0.85rem;
  margin-bottom: 0.7rem;
  text-align: center;
}

.exp-card ul {
  margin: 0;
  padding-left: 1rem;
}

.exp-card li {
  margin-bottom: 0.5rem;
  line-height: 1.4;
  font-size: 0.85rem;
}

/* Mobile-first education */
.edu-block {
  background: #23272f;
  border-radius: 0.8rem;
  padding: 1rem;
  margin-bottom: 1rem;
  box-shadow: 0 1px 8px rgba(97,218,251,0.05);
  text-align: center;
}

.edu-block h3 {
  margin: 0 0 0.3rem 0;
  color: #fff;
  font-size: 1rem;
}

.edu-period {
  color: #b0b0b0;
  font-size: 0.85rem;
}

.certificates {
  margin-top: 1rem;
  text-align: center;
}

.certificates h4 {
  color: #21a1f3;
  margin-bottom: 0.5rem;
}

.certificates ul {
  list-style: disc inside;
  margin: 0;
  padding-left: 1rem;
}

.certificates li {
  margin-bottom: 0.3rem;
  font-size: 0.9rem;
  text-align: left;
}

/* Mobile-first navigation */
.sticky-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 100;
  background: rgba(36, 40, 50, 0.95);
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  transition: background 0.3s, box-shadow 0.3s;
  backdrop-filter: blur(8px);
}

.nav-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  max-width: 1200px;
  margin: 0 auto;
}

.nav-links {
  display: flex;
  gap: 0.5rem;
  overflow-x: auto;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.mobile-menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  height: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 10;
}

.mobile-menu-toggle span {
  width: 25px;
  height: 3px;
  background: #61dafb;
  border-radius: 10px;
  transition: all 0.3s linear;
  position: relative;
  transform-origin: 1px;
}

.sticky-nav a {
  color: #61dafb;
  font-weight: 600;
  text-decoration: none;
  font-size: 0.9rem;
  padding: 0.4rem 0.8rem;
  border-radius: 1rem;
  transition: background 0.2s, color 0.2s;
  white-space: nowrap;
  flex-shrink: 0;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sticky-nav a.active, .sticky-nav a:hover {
  background: #21a1f3;
  color: #fff;
}

.sticky-nav a span {
  display: none;
}

.sticky-nav a::before {
  content: attr(data-label);
  font-size: 0.7rem;
  display: block;
  text-align: center;
  margin-top: 0.1rem;
}

/* Mobile menu styles */
@media (max-width: 600px) {
  .mobile-menu-toggle {
    display: flex;
  }
  
  .nav-links {
    position: fixed;
    top: 0;
    right: -100%;
    height: 100vh;
    width: 250px;
    background: rgba(36, 40, 50, 0.98);
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 80px;
    transition: right 0.3s ease-in-out;
    gap: 1rem;
    overflow-y: auto;
  }
  
  .sticky-nav.mobile-open .nav-links {
    right: 0;
  }
  
  .sticky-nav a {
    width: 200px;
    justify-content: flex-start;
    padding: 1rem 1.5rem;
    border-radius: 0.5rem;
    font-size: 1rem;
  }
  
  .sticky-nav a span {
    display: inline;
    margin-left: 10px;
  }
  
  .sticky-nav a::before {
    display: none;
  }
  
  /* Hamburger animation */
  .mobile-menu-toggle span:first-child {
    transform: rotate(0);
  }
  
  .mobile-menu-toggle span:nth-child(2) {
    opacity: 1;
    transform: translateX(0);
  }
  
  .mobile-menu-toggle span:nth-child(3) {
    transform: rotate(0);
  }
  
  .sticky-nav.mobile-open .mobile-menu-toggle span:first-child {
    transform: rotate(45deg);
  }
  
  .sticky-nav.mobile-open .mobile-menu-toggle span:nth-child(2) {
    opacity: 0;
    transform: translateX(20px);
  }
  
  .sticky-nav.mobile-open .mobile-menu-toggle span:nth-child(3) {
    transform: rotate(-45deg);
  }
  
  /* Mobile menu backdrop */
  .mobile-menu-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    z-index: 99;
    backdrop-filter: blur(2px);
  }
}

/* Mobile-first contact section */
.contact-section {
  margin-top: 1.5rem;
  background: rgba(30, 34, 40, 0.95);
  border-radius: 1rem;
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  padding: 1.5rem 1rem;
  text-align: center;
}

.contact-section h2 {
  color: #61dafb;
  margin-bottom: 1rem;
  font-size: 1.6rem;
  font-weight: 600;
}

.contact-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
}

.contact-link {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background: #23272f;
  padding: 0.8rem 1.2rem;
  border-radius: 1.5rem;
  font-size: 1rem;
  text-decoration: none;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(97,218,251,0.10);
  transition: background 0.2s, color 0.2s, transform 0.2s;
  width: 100%;
  max-width: 280px;
  min-height: 44px;
}

.contact-link:hover {
  background: #21a1f3;
  color: #fff;
  transform: translateY(-2px) scale(1.04);
}

/* Mobile-first hero content */
.hero-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  min-height: 200px;
}

.hero-left {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-bottom: 1rem;
}

.hero-right {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.avatar-illustration {
  background: linear-gradient(135deg, #232526 60%, #61dafb 100%);
  border-radius: 50%;
  box-shadow: 0 4px 24px rgba(97,218,251,0.18), 0 1px 8px rgba(0,0,0,0.12);
  padding: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  margin: 0;
}

.avatar-illustration svg {
  width: 120px;
  height: 120px;
}

/* Profile Photo Styles */
.profile-photo-container {
  background: linear-gradient(135deg, #232526 60%, #61dafb 100%);
  border-radius: 50%;
  box-shadow: 0 4px 24px rgba(97,218,251,0.18), 0 1px 8px rgba(0,0,0,0.12);
  padding: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  margin: 0;
  overflow: hidden;
}

.profile-photo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  border: 3px solid rgba(97, 218, 251, 0.3);
  transition: transform 0.3s ease, border-color 0.3s ease;
}

.profile-photo:hover {
  transform: scale(1.05);
  border-color: rgba(97, 218, 251, 0.6);
}

/* Hide side panels on mobile */
.side-panel {
  display: none;
}

/* Optimize floating code for mobile */
.code-float {
  display: none;
}

.code-float.c1,
.code-float.c3 {
  display: block;
  opacity: 0.03;
  font-size: 0.6rem;
}

/* Animated background - simplified for mobile */
.animated-bg {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0;
  pointer-events: none;
  overflow: hidden;
  background: linear-gradient(120deg, #232526 0%, #414345 100%);
  animation: bgGradientMove 16s ease-in-out infinite alternate;
}

@keyframes bgGradientMove {
  0% { background-position: 0% 50%; filter: brightness(1.1) saturate(1.1); }
  100% { background-position: 100% 50%; filter: brightness(1.2) saturate(1.3); }
}

/* Tablet styles */
@media (min-width: 768px) {
  .hero {
    padding: 2.5rem 1.5rem 2rem 1.5rem;
  }
  
  .hero h1 {
    font-size: 2.5rem;
  }
  
  .subtitle {
    font-size: 1.2rem;
  }
  
  .hero-links {
    gap: 1rem;
  }
  
  .hero-links a {
    padding: 0.7rem 1.2rem;
    font-size: 1rem;
    min-width: 120px;
  }
  
  section {
    max-width: 90%;
    padding: 1.5rem 1.2rem;
    margin: 2rem auto;
  }
  
  section h2 {
    font-size: 1.8rem;
  }
  
  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.2rem;
  }
  
  .skill-category h3 {
    font-size: 1.1rem;
  }
  
  .skill-category li {
    font-size: 1rem;
  }
  
  .exp-card {
    padding: 1.2rem;
  }
  
  .exp-card h3 {
    font-size: 1.2rem;
  }
  
  .exp-card h3 span {
    font-size: 1rem;
  }
  
  .exp-meta {
    font-size: 0.9rem;
  }
  
  .exp-card li {
    font-size: 0.9rem;
  }
  
  .sticky-nav {
    gap: 1rem;
    padding: 0.6rem 0;
  }
  
  .sticky-nav a {
    font-size: 1rem;
    padding: 0.5rem 1rem;
  }
  
  .sticky-nav a span {
    display: inline;
    margin-left: 6px;
  }
  
  .sticky-nav a::before {
    display: none;
  }
  
  .contact-link {
    font-size: 1.1rem;
    padding: 0.8rem 1.5rem;
    max-width: 320px;
  }
  
  .hero-content {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    max-width: 900px;
    min-height: 250px;
  }
  
  .hero-left {
    flex: 1 1 60%;
    align-items: flex-start;
    text-align: left;
    margin-bottom: 0;
  }
  
  .hero-right {
    flex: 1 1 40%;
  }
  
  .avatar-illustration {
    width: 160px;
    height: 160px;
    padding: 1rem;
    margin-left: 2rem;
  }
  
  .avatar-illustration svg {
    width: 160px;
    height: 160px;
  }
  
  /* Profile photo responsive styles for tablet */
  .profile-photo-container {
    width: 160px;
    height: 160px;
    padding: 1rem;
    margin-left: 2rem;
  }
  
  /* Hide mobile menu toggle on tablet */
  .mobile-menu-toggle {
    display: none;
  }
  
  .nav-links {
    position: static;
    height: auto;
    width: auto;
    background: transparent;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0;
    gap: 1rem;
    overflow: visible;
  }
}

/* Desktop styles */
@media (min-width: 1024px) {
  .hero {
    padding: 3rem 2rem 2.5rem 2rem;
  }
  
  .hero h1 {
    font-size: 3rem;
    letter-spacing: 2px;
  }
  
  .subtitle {
    font-size: 1.3rem;
    margin: 0.5rem 0 1.5rem 0;
  }
  
  .hero-links {
    gap: 1.5rem;
  }
  
  .hero-links a {
    padding: 0.5rem 1.2rem;
    font-size: 1.1rem;
    min-width: 140px;
  }
  
  section {
    max-width: 900px;
    padding: 2rem 1.5rem;
    margin: 2.5rem auto;
    border-radius: 1.5rem;
  }
  
  section h2 {
    font-size: 2rem;
    text-align: left;
  }
  
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 1.5rem;
  }
  
  .skill-category {
    padding: 1rem 1.2rem;
    border-radius: 1rem;
  }
  
  .skill-category h3 {
    font-size: 1.1rem;
    text-align: left;
  }
  
  .skill-category li {
    font-size: 1rem;
    text-align: left;
  }
  
  .exp-card {
    padding: 1.5rem;
    border-radius: 1rem;
  }
  
  .exp-card h3 {
    font-size: 1.2rem;
    text-align: left;
  }
  
  .exp-card h3 span {
    font-size: 1rem;
    display: inline;
  }
  
  .exp-meta {
    font-size: 0.95rem;
    text-align: left;
  }
  
  .exp-card li {
    font-size: 1rem;
  }
  
  .sticky-nav {
    gap: 2rem;
    padding: 0.7rem 0;
  }
  
  .sticky-nav a {
    font-size: 1.1rem;
    padding: 0.3rem 1rem;
  }
  
  .contact-link {
    font-size: 1.1rem;
    padding: 0.7rem 1.5rem;
    max-width: 350px;
  }
  
  .avatar-illustration {
    width: 200px;
    height: 200px;
    padding: 1.2rem;
  }
  
  .avatar-illustration svg {
    width: 200px;
    height: 200px;
  }
  
  /* Profile photo responsive styles for desktop */
  .profile-photo-container {
    width: 200px;
    height: 200px;
    padding: 1.2rem;
  }
  
  /* Show side panels on desktop */
  .side-panel {
    display: flex;
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    flex-direction: column;
    align-items: center;
    background: rgba(36, 40, 50, 0.92);
    border-radius: 1.5rem;
    box-shadow: 0 2px 16px rgba(97,218,251,0.08);
    padding: 1.2rem 0.7rem;
    gap: 1.1rem;
    transition: background 0.3s, box-shadow 0.3s;
    min-width: 56px;
  }
  
  .tech-stack-panel {
    left: 2vw;
    animation: sidePanelInLeft 1.2s cubic-bezier(0.77,0,0.175,1) 0.2s both;
  }
  
  .quick-facts-panel {
    right: 2vw;
    min-width: 120px;
    animation: sidePanelInRight 1.2s cubic-bezier(0.77,0,0.175,1) 0.2s both;
  }
  
  @keyframes sidePanelInLeft {
    0% { opacity: 0; transform: translate(-40px, -50%); }
    100% { opacity: 1; transform: translateY(-50%); }
  }
  
  @keyframes sidePanelInRight {
    0% { opacity: 0; transform: translate(40px, -50%); }
    100% { opacity: 1; transform: translateY(-50%); }
  }
  
  .tech-icon {
    color: #61dafb;
    font-size: 2.1rem;
    background: #23272f;
    border-radius: 50%;
    padding: 0.5rem;
    margin: 0.1rem 0;
    box-shadow: 0 2px 8px rgba(97,218,251,0.10);
    transition: background 0.2s, color 0.2s, transform 0.2s;
    cursor: pointer;
    animation: techIconPop 0.7s cubic-bezier(0.77,0,0.175,1) both;
  }
  
  .tech-icon:hover {
    background: #21a1f3;
    color: #fff;
    transform: scale(1.13) rotate(-8deg);
  }
  
  @keyframes techIconPop {
    0% { opacity: 0; transform: scale(0.7) translateY(20px); }
    100% { opacity: 1; transform: scale(1) translateY(0); }
  }
  
  .quick-facts-title {
    color: #61dafb;
    font-weight: 700;
    font-size: 1.1rem;
    margin-bottom: 0.7rem;
    letter-spacing: 1px;
  }
  
  .quick-fact {
    color: #fff;
    font-size: 1rem;
    margin-bottom: 0.4rem;
    text-align: left;
  }
  
  .fact-label {
    color: #21a1f3;
    font-weight: 600;
    margin-right: 0.3rem;
  }
  
  .fact-value {
    color: #fff;
    font-weight: 400;
  }
  
  /* Show floating code on desktop */
  .code-float {
    display: block;
    position: absolute;
    color: #61dafb;
    font-family: 'Fira Mono', 'Menlo', 'Monaco', 'Consolas', monospace;
    font-size: 1.1rem;
    opacity: 0.13;
    pointer-events: none;
    user-select: none;
    white-space: pre;
    animation: codeFloatAnim 18s linear infinite;
  }
  
  .code-float.c1 { left: 10vw; top: 20vh; font-size: 1.3rem; animation-delay: 0s; }
  .code-float.c2 { left: 70vw; top: 60vh; font-size: 1.1rem; animation-delay: 3s; }
  .code-float.c3 { left: 40vw; top: 80vh; font-size: 1.5rem; animation-delay: 6s; }
  .code-float.c4 { left: 80vw; top: 30vh; font-size: 1.2rem; animation-delay: 9s; }
  .code-float.c5 { left: 20vw; top: 70vh; font-size: 1.1rem; animation-delay: 12s; }
  
  @keyframes codeFloatAnim {
    0% { transform: translateY(0) scale(1) rotate(-2deg); opacity: 0.13; }
    50% { transform: translateY(-60px) scale(1.08) rotate(2deg); opacity: 0.18; }
    100% { transform: translateY(0) scale(1) rotate(-2deg); opacity: 0.13; }
  }
}

/* Touch-friendly improvements */
@media (hover: none) and (pointer: coarse) {
  .hero-links a:hover,
  .exp-card:hover,
  .tech-icon:hover,
  .contact-link:hover {
    transform: none;
  }
  
  .sticky-nav a:hover {
    background: #21a1f3;
    color: #fff;
  }
}
